*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: lato, "Times New Roman", serif;
  font-size: 14px;
  line-height: 1.28;
  color: #444444;
  background-color: #ffffff;
}

.main-container {
  flex-grow: 1;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

input,
button,
textarea {
  font-family: oswald, arial, sans-serif;
}

.container {
  width: 100%;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
}

@font-face {
  font-family: "lato";
  src: url("./../../fonts/lato/Lato-Regular.woff2") format("woff2");
  src: url("./../../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "oswald";
  src: url("./../../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("./../../fonts/oswald/oswaldregular.woff2") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
}

.academy-link {
  display: flex;
  justify-content: flex-end;
}

.academy-link__text {
  position: absolute;
  top: 76%;
  left: 24%;
  width: 113px;
  font-size: 16px;
  line-height: 1.25;
  color: #444444;
  transform: translate(-50%, -50%);
}

.academy-link__icon {
  fill: #666666;
}

.button {
  display: block;
  width: 280px;
  height: 40px;
  padding: 8px 40px;
  text-align: center;
  font-family: oswald, arial, sans-serif;
  font-size: 14px;
  line-height: 1.8;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}

.form__meta {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 14px;
  padding-right: 20px;
  padding-bottom: 24px;
  padding-left: 20px;
  border-bottom: 1px solid #d9d9d9;
}
.form__meta .form__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 13px;
}
.form__meta .form__label {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}
.form__meta .form__input {
  flex-grow: 1;
  padding-top: 14px;
  padding-right: 15px;
  padding-bottom: 13px;
  padding-left: 15px;
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.12;
  color: #444444;
  border: 1px solid #e7e7e7;
}
.form__meta .form__input::placeholder {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}

.form__program {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 13px;
  padding-top: 22px;
  padding-right: 20px;
  padding-bottom: 19px;
  padding-left: 20px;
  border-bottom: 1px solid #d9d9d9;
}
.form__program .form__wrapper {
  display: flex;
  align-items: center;
  column-gap: 15px;
}
.form__program .form__radio-input {
  width: 30px;
  height: 30px;
  margin-top: 0;
  margin-right: 0;
  margin-left: 0;
  padding: 7.5px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  appearance: none;
}
.form__program .form__radio-input:checked {
  background: #68b738 content-box;
}
.form__program .form__label {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}

.form__contacts {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 14px;
  padding-top: 26px;
  padding-right: 20px;
  padding-bottom: 24px;
  padding-left: 20px;
}
.form__contacts .form__contacts-title {
  position: relative;
  margin-top: 0;
  margin-bottom: 35px;
  font-family: oswald, arial, sans-serif;
  font-size: 24px;
  line-height: 1.08;
  color: #000000;
}
.form__contacts .form__contacts-title::after {
  content: "";
  position: absolute;
  top: 15px;
  right: -20px;
  width: 93px;
  height: 2px;
  background-color: #68b738;
}
.form__contacts .form__wrapper {
  display: flex;
  align-items: center;
  column-gap: 13px;
}
.form__contacts .form__label {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}
.form__contacts .form__input {
  flex-grow: 1;
  padding-top: 14px;
  padding-right: 46px;
  padding-bottom: 13px;
  padding-left: 15px;
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.12;
  color: #444444;
  border: 1px solid #e7e7e7;
}
.form__contacts .form__input--email {
  background-image: url("./../img/sprite.svg#form-email-icon");
  background-repeat: no-repeat;
}
.form__contacts .form__input::placeholder {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}

.form__comment {
  padding-right: 20px;
  padding-bottom: 23px;
  padding-left: 20px;
}
.form__comment .form__comments-title {
  position: relative;
  margin-top: 0;
  margin-bottom: 35px;
  font-family: oswald, arial, sans-serif;
  font-size: 24px;
  line-height: 1.08;
  color: #000000;
}
.form__comment .form__comments-title::after {
  content: "";
  position: absolute;
  top: 15px;
  right: -20px;
  width: 159px;
  height: 2px;
  background-color: #68b738;
}
.form__comment .form__textarea {
  width: 280px;
  height: 150px;
  padding-top: 14px;
  padding-right: 14px;
  padding-bottom: 14px;
  padding-left: 15px;
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.12;
  color: #444444;
  border: 1px solid #e7e7e7;
  appearance: none;
  resize: none;
}
.form__comment .form__textarea::placeholder {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}

.form__additional {
  padding-right: 20px;
  padding-bottom: 34px;
  padding-left: 20px;
}
.form__additional .form__additional-title {
  position: relative;
  margin-top: 0;
  margin-bottom: 35px;
  font-family: oswald, arial, sans-serif;
  font-size: 24px;
  line-height: 1.08;
  color: #000000;
}
.form__additional .form__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  row-gap: 13px;
}
.form__additional .form__additional-title::after {
  content: "";
  position: absolute;
  top: 15px;
  right: -20px;
  width: 139px;
  height: 2px;
  background-color: #68b738;
}
.form__additional .form__wrapper {
  display: flex;
  align-items: center;
  column-gap: 15px;
}
.form__additional .form__checkbox-input {
  width: 30px;
  height: 30px;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  border: 1px solid #cdcdcd;
  appearance: none;
}
.form__additional .form__checkbox-input:checked {
  background-image: url("./../img/sprite.svg#form-tick-icon");
  background-repeat: no-repeat;
  background-size: 26px 26px;
}
.form__additional .form__label {
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1.12;
  text-transform: uppercase;
  color: #444444;
}

.form__button {
  margin-right: auto;
  margin-bottom: 21px;
  margin-left: auto;
}

.form__button + p {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
}

.hamburger {
  position: relative;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border: none;
}

.hamburger__line {
  position: absolute;
  top: 10px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}

.hamburger__line:nth-child(1) {
  transform: translateY(-8px);
}

.hamburger__line:nth-child(3) {
  transform: translateY(8px);
}

.logo__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigation {
  display: flex;
  align-items: center;
}

.navigation__list {
  display: none;
}

.product-card {
  display: flex;
  flex-direction: column;
  row-gap: 13px;
  padding-top: 11px;
  padding-right: 20px;
  padding-bottom: 14px;
  padding-left: 20px;
  border-bottom: 1px solid #d9d9d9;
}

.product-card__wrapper {
  display: flex;
  width: 280px;
}

.product-card__image-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  max-width: 140px;
}

.product-card__image {
  display: block;
}
.product-card__image--position {
  margin-bottom: 12px;
}

.product-card__meta {
  width: 100%;
  max-width: 140px;
}

.product-card__title {
  width: 76px;
  margin-top: 0;
  margin-bottom: 14px;
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  color: #222222;
}

.product-card__info {
  margin-top: 0;
  margin-bottom: 0;
}

.product-card__info div {
  display: flex;
  justify-content: space-between;
}

.product-card__info dt {
  display: inline;
  font-size: 12px;
  line-height: 1.33;
}

.product-card__info dd {
  display: inline;
  margin-left: 0;
  font-size: 12px;
  line-height: 1.33;
}

.program {
  position: relative;
  padding-top: 30px;
  padding-right: 33px;
  padding-bottom: 18px;
  padding-left: 20px;
  background-color: #f2f2f2;
}
.program--icon_slim::before {
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}
.program--icon_slim::after {
  content: "";
  position: absolute;
  display: block;
  width: 36px;
  height: 50px;
  background-image: url("./../img/svg/sprite.svg#program-slim-icon");
  background-repeat: no-repeat;
  background-size: 36px 50px;
}
.program--icon_pro::before {
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}
.program--icon_pro::after {
  content: "";
  position: absolute;
  display: block;
  width: 67px;
  height: 28px;
  background-image: url("./../img/svg/sprite.svg#program-pro-icon");
  background-repeat: no-repeat;
  background-size: 67px 28px;
}

.program__title {
  margin-top: 0;
  margin-bottom: 29px;
  margin-left: 70px;
  font-family: oswald, arial, sans-serif;
  font-size: 24px;
  line-height: 1.54;
  text-transform: uppercase;
  color: #000000;
}

.program p {
  margin-top: 0;
  margin-bottom: 15px;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;
}

.program__link {
  display: flex;
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
.program__link::after {
  content: "";
  display: block;
  width: 32px;
  height: 10px;
  background-image: url("./../img/svg/sprite.svg#program-short-arrow-icon");
  background-repeat: no-repeat;
  background-size: 32px 10px;
}

.slider__image {
  display: block;
}

.social {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
  column-gap: 24px;
}

.social__link {
  display: inline-flex;
}

.main-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-top: 13px;
  padding-right: 21px;
  padding-bottom: 14px;
  padding-left: 20px;
}

.hero {
  padding-top: 27px;
  padding-right: 20px;
  padding-left: 20px;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("./../img/hero/hero-background.jpg");
  background-repeat: no-repeat;
  background-size: auto 302px;
}

.hero__title {
  margin-top: 0;
  margin-bottom: 25px;
  font-family: oswald, arial, sans-serif;
  font-size: 36px;
  line-height: 1;
  text-align: center;
  color: #ffffff;
}

.hero__slogan {
  margin-top: 0;
  margin-bottom: 29px;
  font-family: oswald, arial, sans-serif;
  font-size: 14px;
  line-height: 1;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
}

.hero__image {
  display: block;
  margin-bottom: 3px;
}

.programs {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  padding: 20px;
}

.process {
  padding-right: 20px;
  padding-bottom: 53px;
  padding-left: 20px;
}

.process__title {
  margin-top: 0;
  margin-bottom: 40px;
  font-family: oswald, arial, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.11;
  color: #000000;
}

.process__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.process__item p {
  display: flex;
  align-items: center;
  column-gap: 20px;
  margin-top: 0;
  margin-bottom: 0;
}
.process__item p::before {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #68b738;
}

.example {
  padding-top: 25px;
  padding-right: 20px;
  padding-bottom: 40px;
  padding-left: 20px;
  background-color: #eaeaea;
}

.example__title {
  margin-top: 0;
  margin-bottom: 40px;
  font-family: oswald, arial, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.11;
  color: #000000;
}

.example p {
  margin-top: 0;
  margin-bottom: 20px;
}

.example__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 34px;
}

.example__weight {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 124px;
  height: 56px;
  margin-top: 0;
  margin-bottom: 0;
  border: 1px solid #cdcdcd;
  border-radius: 2px;
}

.example__weight dt {
  text-align: center;
  font-family: oswald, arial, sans-serif;
  font-size: 24px;
  line-height: 1;
}

.example__weight dd {
  position: absolute;
  z-index: 1;
  top: 80%;
  width: 87px;
  height: 24px;
  margin-left: 0;
  padding-inline: 5px;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  background-color: #eaeaea;
}

.example__time dt {
  text-align: center;
  font-family: oswald, arial, sans-serif;
  font-size: 24px;
  line-height: 1;
}

.example__time {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 124px;
  height: 56px;
  margin-top: 0;
  margin-bottom: 0;
  border: 1px solid #cdcdcd;
  border-radius: 2px;
}

.example__time dd {
  position: absolute;
  z-index: 1;
  top: 80%;
  width: 87px;
  height: 24px;
  margin-left: 0;
  padding-inline: 5px;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  background-color: #eaeaea;
}

.example__cost {
  font-family: oswald, arial, sans-serif;
  font-size: 14px;
  line-height: 1.42;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}

.contacts--background {
  background-color: #f2f2f2;
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 26px 20px;
}

.contacts__promo {
  width: 100%;
  max-width: 140px;
  margin-top: 0;
  margin-bottom: 0;
  font-family: oswald, arial, sans-serif;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  color: #111111;
}

.contacts__address {
  width: 100%;
  max-width: 140px;
  font-style: normal;
  line-height: 1.42;
}

.contacts__map-wrapper {
  position: relative;
}

.contacts__map-image {
  display: block;
}

.contacts__map-link {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
}

.main-footer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  background-color: #f2f2f2;
}

.main-footer__social {
  display: flex;
  justify-content: center;
  width: 280px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.main-footer__academy-link {
  margin-left: auto;
}

.selection {
  padding-top: 25px;
  padding-bottom: 32px;
  border-top: 1px solid #d9d9d9;
}

.selection__title {
  margin-top: 0;
  margin-bottom: 37px;
  padding-right: 20px;
  padding-left: 20px;
  font-family: oswald, arial, sans-serif;
  font-size: 36px;
  line-height: 1;
  color: #000000;
}

.selection__title + p {
  width: 264px;
  margin-top: 0;
  margin-bottom: 39px;
  padding-right: 20px;
  padding-left: 20px;
  font-family: oswald, arial, sans-serif;
  font-size: 14px;
  line-height: 1.28;
  text-transform: uppercase;
  color: #000000;
}

.catalog {
  padding-top: 27px;
  padding-bottom: 25px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.catalog__title {
  margin-top: 0;
  margin-bottom: 0;
  padding-right: 20px;
  padding-bottom: 41px;
  padding-left: 20px;
  font-family: oswald, arial, sans-serif;
  font-size: 36px;
  line-height: 1;
  color: #000000;
  border-bottom: 1px solid #d9d9d9;
}

.catalog__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}